/**
 * Title:
 *    Icon Module
 * Description:
 *    Responsible for all sizes and styling of Grunticon based icons
 * Sections:
 *    $. Setup
 *    $. Base
 *    $. Variations
 *
 * TODO:
 *    - Change .svg sizes based on this file for better IE8 support
 */



/* $. Setup
\*----------------------------------------------------------------*/

@mixin icon-size( $size ) {
    width: $size;
    height: $size;
}


/* $. Base
\*----------------------------------------------------------------*/

.icon {
    @include icon-size($icon);
    display: inline-block;
    background-size: contain;
}



/* $. Variations
\*----------------------------------------------------------------*/

/* Generic sizing */
.icon--small { @include icon-size($icon-small); }
.icon--medium { @include icon-size($icon-medium); }
.icon--large { @include icon-size($icon-large); }
.icon--xlarge { @include icon-size($icon-xlarge); }

/* Styling */
.icon--transparent { opacity: .3; filter: alpha(opacity=30); }

/* Logos */
.icon--logo {
}

.icon--twitter {
    background-position: center center;
}

span.icon {
    background-position: center center;
}

span.icon--mail {
    background-position: center bottom;
}

span.icon--clock {
    display: block;

    @include respond-min($bp3) {
        display: inline-block;
        position: relative;
        top: 10px;
    }
}


