/* $. Base
\*----------------------------------------------------------------*/

.container {
    margin: 0 auto;
    width: 92.5%;
    min-width: 15em; // 240px
    max-width: 68.5em; // 1233px

    @include respond-min($bp1) {
        width: 85%;
        position: relative;
    }
}

