 *    $. Placeholders
 */



/* $. Basics
\*----------------------------------------------------------------*/

form {
    @extend %cf;
    margin: 0 0 ($base*1.5) 0;
}

fieldset {
    margin-bottom: $base*1.5;
    padding: 0 $base;

    &:last-child {
        margin-top: 0;
    }
}

legend {
    font-size: 1.25em;
    margin-bottom: $base;
    padding: 0 1em;
}

label {
    display: block;
    padding-bottom: $base/4;
}

input[type=checkbox] + label,
input[type=radio] + label {

        display: inline-block;

}

