input[type=text],
input[type=number],
input[type=email],
input[type=tel],
select,
textarea {
    width: 100%;
    padding: $base*0.75;
    border: 1px solid;
    border-radius: 0;
    color: #666;
    background-color: #FFF;
    border-color: #CCC;
    -webkit-appearance: none;

    &:hover,
    &:active,
    &:focus {
        border-color: $grey;
    }
}

textarea {
    resize: vertical;
}

form ul,
form ol {
    list-style: none;
    margin-left: 0;
    margin-bottom: $base;
}

form li {
    margin-bottom: $base/2;
}

form li:last-child,
fieldset:last-child {
    margin-bottom: 0;
}

fieldset {
    padding: 0;
    margin: 0;
    border: 0;
    min-width: 0;
}


