/* $. Media Queries - https://github.com/jakearchibald/sass-ie
\*----------------------------------------------------------------*/
/* $. Base - Project defaults & Element styling
\*----------------------------------------------------------------*/
/**
 * Title:
 *    Defaults
 * Description:
 *    These are the base styles for the project
 * Sections:
 *    $. Defaults
 *    $. Selections
 */
/* $. Defaults
\*----------------------------------------------------------------*/
*,
*:before,
*:after {
  box-sizing: inherit;
  -webkit-font-smoothing: antialiased; }

html {
  font-family: "Open Sans", sans-serif;
  line-height: 1.5;
  font-size: 100%;
  overflow-y: scroll;
  box-sizing: border-box; }
  @media screen and (min-width: 62em) {
    html {
      font-size: 112.5%; } }

body {
  color: #505050;
  background-color: #FFFFFF; }

/* $. Selections
\*----------------------------------------------------------------*/
::-moz-selection {
  background: rgba(102, 102, 102, 0.5);
  color: #FFF;
  text-shadow: none; }

::selection {
  background: rgba(102, 102, 102, 0.5);
  color: #FFF;
  text-shadow: none; }

:focus {
  outline: 0;
  box-shadow: 0 0 1px #e6e6e6; }

/**
 * Title:
 *    Elements
 * Description:
 *    The styling of basic element tags
 * Sections:
 *    $. Basics
 *    $. Links
 *    $. Blockquotes
 *    $. Embedded
 */
/* $. Basics
\*----------------------------------------------------------------*/
hr {
  border: 0;
  border-top: 1px solid #e6e6e6; }

ins {
  text-decoration: none;
  color: #000;
  background: #ff9; }

mark {
  font-style: italic;
  font-weight: bold; }

pre {
  overflow-x: auto; }

address {
  white-space: pre-line; }

/* $. Links
\*----------------------------------------------------------------*/
a {
  text-decoration: none;
  color: #59863A; }
  a:hover {
    color: #41622b; }

a[href^="tel"] {
  text-decoration: none; }

/* $. Blockquotes
\*----------------------------------------------------------------*/
blockquote {
  clear: both;
  margin-left: 0;
  margin-right: 0;
  font-style: italic; }
  @media screen and (min-width: 48em) {
    blockquote {
      padding-left: 2em;
      padding-right: 2em; } }
  blockquote p {
    font-size: 1.35em; }

/* $. Embedded
\*----------------------------------------------------------------*/
img {
  display: inline-block;
  margin: 0 auto;
  font-style: italic;
  color: #666;
  vertical-align: middle;
  max-width: 100%;
  height: auto; }

img,
audio,
canvas,
embed,
object,
video {
  max-width: 100%; }

iframe {
  display: block; }

/**
 * Title:
 *    Forms
 * Description:
 *    Styling of forms and form variations
 * Sections:
 *    $. Basics
 *    $. Placeholders
 */
/* $. Basics
\*----------------------------------------------------------------*/
form {
  margin: 0 0 1.5em 0; }

fieldset {
  margin-bottom: 1.5em;
  padding: 0 1em; }
  fieldset:last-child {
    margin-top: 0; }

legend {
  font-size: 1.25em;
  margin-bottom: 1em;
  padding: 0 1em; }

label {
  display: block;
  padding-bottom: 0.25em; }

input[type=checkbox] + label,
input[type=radio] + label {
  display: inline-block; }

input[type=text],
input[type=number],
input[type=email],
input[type=tel],
select,
textarea {
  width: 100%;
  padding: 0.75em;
  border: 1px solid;
  border-radius: 0;
  color: #666;
  background-color: #FFF;
  border-color: #CCC;
  -webkit-appearance: none; }
  input[type=text]:hover, input[type=text]:active, input[type=text]:focus,
  input[type=number]:hover,
  input[type=number]:active,
  input[type=number]:focus,
  input[type=email]:hover,
  input[type=email]:active,
  input[type=email]:focus,
  input[type=tel]:hover,
  input[type=tel]:active,
  input[type=tel]:focus,
  select:hover,
  select:active,
  select:focus,
  textarea:hover,
  textarea:active,
  textarea:focus {
    border-color: #888888; }

textarea {
  resize: vertical; }

form ul,
form ol {
  list-style: none;
  margin-left: 0;
  margin-bottom: 1em; }

form li {
  margin-bottom: 0.5em; }

form li:last-child,
fieldset:last-child {
  margin-bottom: 0; }

fieldset {
  padding: 0;
  margin: 0;
  border: 0;
  min-width: 0; }

