 */
/* $. Vertical Rhythm
\*----------------------------------------------------------------*/
h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0.5em;
  line-height: 1; }

blockquote, pre, p, ul, ol, dl, hr {
  margin-top: 0; }

p, ul, ol, dl, hr, form, table, blockquote, pre {
  margin-bottom: 1em; }

ul > :last-child,
ol > :last-child,
dl > :last-child,
blockquote > :last-child {
  margin-bottom: 0; }

/* $. Headings and Common Styles
\*----------------------------------------------------------------*/
h1, .alpha {
