@media screen and (min-width: 78em) {
  .card__content a {
    display: inline-block; } }

.card__content--home p {
  margin-bottom: 0; }

@media screen and (min-width: 78em) {
  .card__content--home {
    height: 100%; } }

.card-list {
  width: 100%;
  margin: 0;
  overflow: hidden;
  margin-top: 1em; }
  @media screen and (min-width: 78em) {
    .card-list {
      column-count: 2; } }

.card-list__item {
  font-size: 1.3rem;
  list-style: none;
  position: relative; }
  @media screen and (min-width: 78em) {
    .card-list__item {
      line-height: 1.3; } }

.card-list__item:before, .card__title:before {
  margin-right: 0.5em;
  color: #59863A;
  font-weight: 600;
  content: "\003e";
  position: absolute;
  left: 0; }

.card-list__item > a, .card__title > a {
  margin-left: 1.8rem;
  line-height: 1.3;
  vertical-align: text-top; }

.card__title {
  position: relative;
  padding-left: 1.8rem;
  line-height: 1.3; }

/* $. Variations
\*----------------------------------------------------------------*/
@media screen and (min-width: 62em) {
  .card--introductory .card__content {
    width: calc(50% - 1em);
    float: left; } }

.card--introductory .card__image {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 280px; }
  @media screen and (min-width: 62em) {
    .card--introductory .card__image {
      width: calc(50% - 3em);
      float: right;
      height: 280px; } }

.card--featured {
